.foam-goods {
  padding: 0 60px;
  margin-top: 50px;
}

.foam-goods::after {
  content: '';
  display: block;
  height: 2px;
  width: 100%;
  background: #0f0d0e;
  margin-top: 100px;
}

.goods {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.goods-item {
  max-width: 37.71vw;
  margin-bottom: 78px;
  transition: 0.35s;
}

.goods-item.active {
  z-index: 102;
  border: none;
}

.goods-item:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  filter: none;
}

// .goods-item__img {
//   cursor: url('../images/order-btn.svg'), auto;
// }

.good-item__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
  padding-top: 40px;
  position: relative;
}

.order-btn {
  width: 6.25vw;
  height: 6.25vw;
  border-radius: 50%;
  border: none;
  background-color: #0f0d0e;
  font-style: normal;
  font-weight: bold;
  font-size: 0.63vw;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  right: 20%;
  bottom: -3.02vw;
  opacity: 0;
  transition: 0.5s ease-in-out;
}

// .goods-item:hover {
//   .order-btn {
//     opacity: 1;
//   }
// }

.order-btn.active {
  display: block;
}

.good-item__title::after {
  content: '';
  display: block;
  height: 2px;
  max-width: 724px;
  background: #e8e7e8;
  margin-top: 40px;
}

.good-item__list {
  margin-top: 40px;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #3d3b3c;
  margin-bottom: 60px;
}

.list-info__icon {
  cursor: pointer;
}

.good-item__list-item {
  display: flex;
  align-items: center;
}

.good-item__list-item img {
  margin-left: 4px;
  margin-bottom: 2px;
}

.goods-item_description {
  margin-top: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #3d3b3c;
}

.goods-item__table {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #3d3b3c;
  margin-top: 20px;
  cursor: pointer;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 271px;
  background-color: #0f0d0e;
  font-weight: bold;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #fff;
  padding: 20px;
  position: absolute;
  z-index: 1;
  left: 150%;
  top: -4px;
}

.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 17%;
  right: 100%;
  margin-top: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: transparent #0f0d0e transparent transparent;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}

.backdrop {
  position: fixed;
  left: -100%;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)), url(ezgif);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.backdrop.active {
  left: 0;
  opacity: 1;
  z-index: 101;
}

.goods-item_description a {
  text-decoration: none;
  color: #c81947;
}

.foam-description__subtext a {
  text-decoration: none;
  color: #c81947;
}

@media (max-width: 1050px) {
  .goods-item {
    max-width: 640px;
    margin-bottom: 78px;
  }

  .order-btn {
    width: 100px;
    height: 100px;
    font-size: 12px;
    bottom: -47px;
    right: 5%;
  }

  .foam-goods::after {
    margin-top: 50px;
  }
}

@media (max-width: 768px) {
  .order-btn {
    width: 100px;
    height: 100px;
    bottom: -47px;
    right: 5%;
  }
}

@media (max-width: 576px) {
  .foam-goods {
    padding: 0 20px;
  }

  .foam-goods::after {
    margin-top: 0;
  }

  .order-btn {
    font-size: 10px;
  }

  .tooltip .tooltiptext {
    visibility: hidden;
    width: 270px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    margin-left: -60px;
    left: 0;
    top: 35px;
  }

  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent black transparent;
    margin-top: -20px;
  }
}

@media (max-width: 440px) {
  .good-item__title {
    font-size: 40px;
  }

  .order-btn {
    width: 80px;
    height: 80px;
    bottom: -38px;
  }

  .goods-item:hover {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
    filter: none;
  }
}

@media (max-width: 380px) {
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 270px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
    margin-left: -75px;
    left: 0;
    top: 35px;
  }

  .tooltip .tooltiptext::after {
    display: none;
  }
}
