.molding-service {
  padding: 0 60px;
  margin-top: 200px;
  padding-bottom: 150px;
}

.molding {
  display: flex;
  justify-content: space-between;
}

.molding-item {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 80px;
  width: 470px;
  margin-right: 20px;
}

.molding-btn-block {
  margin-top: 100px;
  display: flex;
  justify-content: center;
}

.molding-btn {
  border: 1px solid #0f0d0e;
  border-radius: 50px;
  padding: 12px 22px 11px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  width: 270px;
  background-color: #fff;
  color: #0f0d0e;
}

@media (max-width: 992px) {
  .molding-service {
    margin-top: 25px;
    padding: 0 20px;
  }
}

@media (max-width: 576px) {
  .molding {
    display: block;
  }

  .molding-item {
    width: 100%;
    margin-top: 30px;
  }

  .molding-title {
    line-height: 45px;
  }

  .molding-btn-block {
    margin-bottom: 50px;
    margin-top: 40px;
  }
}

@media (max-width: 430px) {
  .molding-btn {
    padding: 12px 14px 11px;
    font-size: 10px;
  }
}
