.about-company-description {
  padding: 0 60px;
  position: relative;
  top: -500px;
}

.about-company-description__bg {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-company-description__bg iframe {
  width: 100%;
  height: 600px;
}

.about-company-description__bg img {
  width: 100%;
}

.about-company__description-list {
  margin-bottom: -380px;
  margin-top: 50px;
}

.description-list__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.description-list__content::after {
  content: '';
  display: flex;
  width: 100%;
  height: 1px;
  background: #0f0d0e;
  margin: 100px 0;
}

.description-list__content:last-child::after {
  display: none;
}

.content-item__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 724px;
}

.content-item__subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #3d3b3c;
  max-width: 472px;
  margin-top: 60px;
}

.content-item__text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 469px;
  margin-bottom: 20px;
}

.content-item__subtitle-link {
  text-decoration: none;
  color: #c81947;
}

.content-item__last-text {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #3d3b3c;
  max-width: 469px;
}

.content-item__last-text:first-child::before {
  content: '';
  display: block;
  width: 32px;
  height: 2px;
  background: #c81947;
  margin-bottom: 34px;
}

.about-company__play {
  position: absolute;
  cursor: pointer;
}

@media (max-width: 1800px) {
  .about-company-description {
    top: -300px;
    margin-bottom: 380px;
  }
}

@media (max-width: 1400px) {
  .about-company-description {
    top: -130px;
  }

  .description-list__content:last-child {
    padding-bottom: 150px;
  }
}

@media (max-width: 1300px) {
  .description-list__content-item {
    margin-top: 50px;
  }
}

@media (max-width: 992px) {
  .about-company-description {
    top: 0;
  }

  .description-list__content::after {
    margin: 50px 0;
  }
}

@media (max-width: 576px) {
  .about-company-description {
    padding: 0 20px;
  }
}

@media (max-width: 546px) {
  .about-company__description-list {
    margin-top: 25px;
  }

  .content-item__title {
    font-size: 36px;
    line-height: 44px;
  }

  .content-item__subtitle {
    margin-top: 30px;
  }

  .description-list__content-item {
    margin-top: 30px;
  }

  .description-list__content::after {
    margin: 30px 0;
  }

  .description-list__content:last-child {
    padding-bottom: 40px;
  }
}

@media (max-width: 400px) {
  .content-item__title {
    font-size: 27px;
    line-height: 35px;
  }
}

.content-item__subtitle a {
  text-decoration: none;
  color: #c81947;
}
