.order-popup {
  position: fixed;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  height: 100vh;
  transition: 1s;
  z-index: 103;
  display: none;
}

.order-popup-content {
  position: fixed;
  right: -50%;
  width: 50%;
  background-color: #fff;
  height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: 103;
  // transform: translateX(200%);
  transition: 1s;
}

.order-popup-content.visible {
  transform: translateX(0);
}

.order-popup-header {
  display: flex;
  align-items: center;
  margin-top: 60px;
}

.close-order-popup {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 80px;
  background-color: #fff;
  font-size: 20px;
  border-radius: 50%;
  margin-left: -40px;
  position: absolute;
  top: 22px;
  cursor: pointer;
}

.order-popup-title {
  width: 100%;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-right: 40px;
}

.order-popup-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 52px;

  .order-popup__input {
    width: 472px;
    border: none;
    border-bottom: 1px solid #0f0d0e;
    padding: 6px 0;
    margin-top: 40px;
    font-weight: normal;
    font-size: 20px;
    line-height: 120%;
    text-transform: uppercase;
    color: #0f0d0e;
  }

  .order-popup__input::placeholder {
    font-size: 12px;
    line-height: 180%;
    text-transform: uppercase;
    color: #777677;
  }

  .order-popup__btn {
    border: none;
    background-color: #fff;
    font-weight: normal;
    font-size: 46px;
    line-height: 55px;
    text-align: center;
    text-transform: uppercase;
    margin-top: 242px;
    color: #0f0d0e;
  }
}

.pure-material-textfield-outlined {
  position: relative;
  display: inline-block;
  padding-top: 5px;
  overflow: hidden;
}

.pure-material-textfield-outlined > input + span {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  max-height: 100%;
  font-size: 11px;
  line-height: 77px;
  cursor: text;
  transition: color 0.2s, font-size 0.2s, line-height 0.2s;
  color: #777677;
  text-transform: uppercase;
}

/* eslint-disable-next-line */
input:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

/* eslint-disable-next-line */
input:focus:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

.pure-material-textfield-outlined > input:not(:focus):placeholder-shown + span {
  font-weight: normal;
  font-size: 12px;
  text-transform: uppercase;
  color: #777677;
  line-height: 120px;
  padding: 3px;
  left: 0;
}

/* eslint-disable-next-line */
input:focus:required:invalid + span {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

input:invalid {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

input:focus:invalid {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

input:focus:required:invalid {
  border-color: #c81947;
  color: #c81947;
  box-shadow: none;
}

.order-popup-success {
  position: absolute;
  width: 49%;
  height: 100vh;
  z-index: 103;
  right: 0;
  background-color: #fff;
  display: none;
}

.order-popup-success__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.submit-title {
  margin-top: 100px;
}

.submit-text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 388px;
  margin-bottom: 25px;
}

.close-order-popup__submit {
  display: none;
}

@media (max-width: 1200px) {
  .order-popup-form .order-popup__input {
    width: 335px;
  }

  .order-popup-form .order-popup__btn {
    margin-top: 170px;
  }
}

@media (max-width: 890px) {
  .order-popup-content {
    width: 100%;
  }

  .order-popup-success {
    width: 100%;
  }

  .close-order-popup__submit {
    display: block;
    left: 9%;
    top: 12%;
  }

  .close-order-popup {
    margin-left: 0;
  }
}

@media (max-height: 870px) {
  .order-popup-form {
    margin-top: 0;
  }

  .order-popup-form .order-popup__btn {
    margin-top: 30px;
    font-size: 30px;
  }
}

@media (max-height: 600px) {
  .order-popup-form .order-popup__input {
    margin-top: 20px;
  }

  .pure-material-textfield-outlined > input + span {
    opacity: 0;
  }

  .pure-material-textfield-outlined > input:not(:focus):placeholder-shown + span {
    line-height: 90px;
    opacity: 1;
  }
}

@media (max-height: 500px) {
  .order-popup-header {
    margin-top: 40px;
  }

  .close-order-popup {
    top: 10px;
  }

  .pure-material-textfield-outlined > input:not(:focus):placeholder-shown + span {
    line-height: 60px;
  }

  .order-popup-form .order-popup__input {
    margin-top: 5px;
  }

  .order-popup-form .order-popup__btn {
    margin-top: 10px;
    font-size: 26px;
  }
}

@media (max-width: 420px) {
  .close-order-popup__submit {
    top: 7%;
  }
}

@media (max-height: 400px) {
  .submit-title {
    margin-top: 5px;
  }

  .submit-text {
    margin-bottom: 7px;
  }
}
