.contacts-info {
  padding: 0 120px;
  margin-top: 260px;
}

.contacts-title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
}

.contacts-main-title-link {
  color: #0f0d0e;
  text-decoration: none;
}

.contacts-main-title-link:hover {
  color: #c81947;
}

.contacts-wrapper {
  display: flex;
  justify-content: space-between;
}

.contacts-wrapper__item-tel {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
}

.contacts-title-link {
  color: #0f0d0e;
  text-decoration: none;
}

.contacts-title-link:hover {
  color: #c81947;
}

.contacts-wrapper__item-text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 663px;
  text-decoration: none;
  margin-top: 50px;
}

.contacts-wrapper__item-link {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 663px;
  text-decoration: none;
  margin-top: 50px;
}

.contacts-wrapper__item-inst:hover {
  color: #c81947;
}

@media (max-width: 1150px) {
  .contacts-wrapper {
    display: block;
  }
}

@media (max-width: 992px) {
  .contacts-info {
    padding: 0 60px;
    margin-top: 50px;
  }

  .foam-header-bg-container img {
    object-fit: cover;
  }
}

@media (max-width: 760px) {
  .contacts-info {
    padding: 0 20px;
    margin-top: 50px;
  }
}

@media (max-width: 400px) {
  .contacts-info {
    margin-top: 80px;
  }

  .contacts-title {
    font-size: 22px;
    line-height: 55px;
  }

  .contacts-wrapper__item-tel {
    font-size: 15px;
    margin-top: 25px;
  }

  .contacts-wrapper__item-text {
    font-size: 15px;
    margin-top: 50px;
  }

  .contacts-wrapper__item-link {
    font-size: 15px;
  }
}
