.foam-description-wrapper {
  padding: 0 60px;
  padding-top: 50px;
  background-color: #fff;
}

.foam-description {
  display: flex;
  justify-content: space-between;
}

.foam-description__item:first-child {
  max-width: 724px;
}

.foam-description__text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;

  span {
    text-decoration-line: underline;
  }
}

.foam-description__subtext {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 60px;
}

.foam-description-collection {
  display: flex;
  justify-content: center;
}

.collection-item {
  max-width: 116px;
}

.collection-item:nth-child(2) {
  margin-left: 124px;
  margin-right: 144px;
}

.collection-item__icon {
  background: #f4f4f4;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.collection-item__title {
  font-style: normal;
  font-weight: bold;
  font-size: 11px;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 22px;
}

.collection-clarify {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #c81947;
}

.collection-clarify::before {
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  background: #e8e7e8;
  margin-top: 60px;
  margin-bottom: 20px;
}

.personal-foam {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  width: 100%;
  letter-spacing: 1px;
}

.personal-foam::before {
  content: '';
  display: block;
  height: 1px;
  width: 100%;
  background: #0f0d0e;
  margin-top: 89px;
  margin-bottom: 40px;
}

@media (max-width: 1500px) {
  .foam-description__item {
    width: 550px;
  }

  .foam-description__item:first-child {
    width: 550px;
  }

  .collection-item:nth-child(2) {
    margin-left: 110px;
    margin-right: 110px;
  }
}

@media (max-width: 1250px) {
  .foam-description__item {
    width: 350px;
  }

  .foam-description__item:first-child {
    width: 460px;
  }

  .collection-item:nth-child(2) {
    margin-left: 25px;
    margin-right: 0;
  }

  .foam-description__text {
    font-size: 18px;
  }
}

@media (max-width: 992px) {
  .foam-description-wrapper {
    margin-top: 50px;
    padding-top: 0;
  }

  .foam-description {
    display: block;
  }

  .foam-description__item {
    width: 100%;
  }

  .foam-description__item:last-child {
    margin-top: 100px;
  }

  .foam-description-collection {
    justify-content: center;
    padding-bottom: 0;
  }

  .collection-item:nth-child(2) {
    margin-left: auto;
    margin-right: auto;
  }

  .about-collection {
    padding-top: 80px;
  }
}

@media (max-width: 768px) {
  .foam-description-collection-mobile {
    flex-direction: row;
    justify-content: space-between;
  }

  .collection-item:nth-child(2) {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (max-width: 565px) {
  .foam-description-wrapper {
    margin-top: 50px;
    padding: 0 20px;
  }

  .foam-description__item:last-child {
    margin-top: 40px;
  }

  .collection-item-mobile {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 110px;
  }

  .collection-item__title {
    font-size: 11px;
  }

  .foam-description-collection {
    padding-bottom: 0;
  }

  .foam-description-collection-mobile {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
  }

  .foam-description__item:first-child {
    width: 300px;
  }

  .collection-clarify::before {
    margin-top: 30px;
  }
}
