.foam-service-adv {
  padding: 0 60px;
  margin-top: 200px;
}

.service-adv__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
}

.service-adv {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service-adv__item {
  width: 472px;
  margin-bottom: 133px;
}

.service-adv__item-title {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #151314;
  margin-top: 30px;
}

.service-adv__item:last-child {
  display: flex;
  align-items: center;
  justify-content: center;
}

.service-adv__btn {
  background-color: #c81947;
  border: none;
  border-radius: 50px;
  padding: 12px 22px 11px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}

@media (max-width: 992px) {
  .service-adv__title {
    font-size: 37px;
  }

  .foam-service-adv {
    padding: 0 60px;
    margin-top: 0;
  }

  .service-adv__item {
    margin-bottom: 90px;
  }
}

@media (max-width: 576px) {
  .foam-service-adv {
    padding: 0 20px;
    margin-top: 50px;
  }

  .service-adv__item {
    margin-bottom: 70px;
  }
}

@media (max-width: 430px) {
  .service-adv__title {
    font-size: 22px;
    line-height: 34px;
  }

  .service-adv__item-title {
    font-size: 16px;
  }
}
