.footer {
  background: #0f0d0e;
  color: #fff;
  padding: 0 60px;
  padding-bottom: 50px;
  transition: 1s;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.link-wrapper {
  padding-top: 120px;
  display: flex;
  justify-content: center;
}

.footer-link-wrapper {
  max-width: 992px;
}

.footer-link::after {
  content: '';
  display: block;
  height: 1px;
  width: inherit;
  background-color: #fff;
  opacity: 0;
  transition: 1s;
}

.footer-link-wrapper:hover {
  .footer-link::after {
    opacity: 1;
  }
}

.footer-link {
  font-weight: normal;
  font-size: 70px;
  line-height: 84px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
}

.footer-image-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 135px;
}

.footre-image-wrapper__img {
  width: 94px;
  height: 94px;
  transition: 0.5s ease-in-out;
}

.footer-slider {
  overflow-x: hidden;
}

.mobile-footer-image__slider-wrapper {
  display: none;
}

