.image-link {
  font-weight: bold;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  margin-left: 32px;
  margin-right: 153px;

  i {
    color: #c81947;
    margin-left: 16px;
  }
}

.footer-wrapper {
  display: flex;
  margin-top: 100px;
  justify-content: space-between;
}

.footer-list__item {
  margin-bottom: 20px;
}

.footer-list__link {
  text-decoration: none;
  font-weight: bold;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #fff;
  transition: 0.2s ease-out;
}

.footer-list__link-inactive {
  pointer-events: none;
  cursor: default;
}

.footer-list__link:hover {
  color: #c81947;
}

.footer-wrap__item {
  display: flex;
  align-items: self-start;

  .footer-list__link {
    text-decoration: none;
    color: #fff;
    font-weight: normal;
    font-size: 11px;
    line-height: 140%;
    letter-spacing: 0.01em;
    text-transform: uppercase;
  }

  .footer-list__item {
    margin-bottom: 6px;
  }

  .footer-list__item:nth-child(2) {
    margin-top: 20px;
  }
}

.footer-logo {
  display: flex;
  align-items: center;

  img {
    margin-right: 30px;
  }
}

.footer-logo::after {
  content: '';
  display: flex;
  width: 1px;
  height: 26px;
  background-color: rgba(255, 255, 255, 0.2);
  margin-right: 30px;
  margin-left: 30px;
}

.footer-list {
  display: flex;
  flex-direction: column;
  margin-right: 178px;
}

.footer-list:last-child {
  margin-right: 0;
}

.footer-item {
  display: flex;
}

.development {
  font-weight: normal;
  font-size: 11px;
  line-height: 140%;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin-left: 56px;
  margin-top: 20px;
  color: #fff;
  text-decoration: none;
}

.fa-heart {
  transition: 1s;
}

.hidden {
  display: none;
}

.footer-list__item_upper {
  text-transform: uppercase;
}

@media (max-width: 1600px) {
  .footer-link {
    font-size: 60px;
  }

  .image-link {
    margin-left: 0;
    margin-right: 0;
  }

  .footre-image-wrapper__img {
    width: 70px;
    height: 70px;
  }

  // .footre-image-wrapper__img:hover {
  //   width: 100px;
  //   margin-top: -40px;
  // }

  .image-link i {
    margin-left: 3px;
  }

  .footer-list {
    margin-right: 0;
  }

  .footer-logo img {
    margin-right: 15px;
  }

  .footer-logo::after {
    margin-right: 15px;
    margin-left: 15px;
  }

  .footer-list__item {
    width: 180px;
  }
}

@media (max-width: 992px) {
  .footer {
    height: initial;
  }

  .footer-link {
    font-size: 47px;
    line-height: 50px;
  }

  .footer-image-wrapper {
    flex-wrap: wrap;
    margin-top: 145px;
  }

  .footer-image-link {
    order: -1;
    flex-basis: 100%;
    margin-bottom: 50px;
  }

  .footer-wrapper {
    flex-wrap: wrap;
  }

  .footer-item:last-child {
    margin-top: 40px;
  }

  .footer-list:first-child {
    margin-right: 85px;
  }
}

@media (max-width: 768px) {
  .footer-image-wrapper__link:last-child {
    display: none;
  }

  .footer-image-wrapper__link:nth-child(2) {
    display: none;
  }

  .footer-image-wrapper__link:nth-child(3) {
    display: none;
  }

  .footer-list__item-menu:first-child {
    margin-right: 20px;
  }

  .footer-mobile-list {
    margin-left: 0;
  }

  .footer-item {
    width: 100%;
  }

  .footer-item:first-child {
    order: 1;
    margin-top: 40px;
    position: relative;
  }

  .footer-item:last-child {
    flex-basis: 100%;
  }

  .footer-wrapper {
    flex-wrap: wrap;
    margin-top: 10px;
  }

  .footer-wrap__item {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .footer-logo {
    flex-direction: column;
    margin-left: -65px;
  }

  .footer-logo::after {
    width: 199px;
    height: 1px;
    margin: 20px 0;
  }

  .footer-logo img {
    position: absolute;
    left: 0;
    margin-right: 0;
  }

  .development {
    text-align: center;
    margin-left: -135px;
    margin-top: 50px;
  }

  .footer-image-wrapper {
    display: none;
  }

  .mobile-footer-image__slider-wrapper {
    display: block;
    margin-top: 50px;
  }
}

@media (max-width: 576px) {
  .footer {
    padding-left: 30px;
    padding-right: 30px;
  }

  .link-wrapper {
    padding-top: 70px;
  }

  .footer-image-wrapper {
    margin-top: 60px;
  }

  .footer-wrapper {
    margin-top: 25px;
  }

  .footer-image-wrapper__link:nth-child(4) {
    display: none;
  }

  .footer-image-wrapper__link:nth-child(5) {
    display: none;
  }

  .footer-list {
    margin-right: 0;
  }

  .footer-list__item-menu:first-child {
    margin-right: -20px;
  }

  .footer-list:first-child {
    margin-right: 0;
  }

  .footer-logo {
    margin-left: 17px;
  }

  .development {
    margin-left: -54px;
  }

  // .footre-image-wrapper__img:hover {
  //   width: 60px;
  //   margin-top: 0;
  // }
}

@media (max-width: 430px) {
  .link-wrapper {
    margin-top: 0;
  }

  .footer-link {
    font-size: 19px;
  }

  .image-link {
    font-size: 15px;
  }

  .footer-image-wrapper__link:nth-child(6) {
    display: none;
  }

  .footer-slider__img {
    height: 70px;
  }

  .footer-slider__img a img {
    height: 70px;
  }
}

@media (max-width: 375px) {
  .footer-list__item-menu:first-child {
    margin-right: -70px;
  }
}
