html {
  height: 100%; // Footer at bottom
  font-size: 62.5%;
  overflow-x: hidden !important;
  scrollbar-color: #fff #333537;
  scrollbar-width: thin;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  height: 100%; // Footer at bottom
  color: #000;
  font-size: 16px;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'PT Root UI', sans-serif;
  font-weight: normal;
  font-style: normal;

  &::-webkit-scrollbar {
    width: 5px;
    z-index: 100;
    background-color: #333537;
  }

  &::-webkit-scrollbar-track {
    background-clip: content-box;   /* THIS IS IMPORTANT */
  }

  &::-webkit-scrollbar-thumb {
    background-color: #c9c9c9;
    border-width: 3px 0 2px;
    border-style: solid;
    border-color: #c9c9c9;
    box-shadow: 0 -100vh 0 100vh #c9c9c9;
  }
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  flex: 1 0 auto; // Footer at bottom
  overflow: hidden;
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}
