.about-company-quality {
  margin-top: 200px;
  padding: 0 60px;
  position: relative;
  top: -500px;
}

.about-company-quality__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
}

.about-company-quality__info {
  margin-top: 60px;
  display: flex;
  justify-content: space-between;
}

.quality-info__text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #3d3b3c;
  max-width: 600px;
  margin-bottom: 40px;
}

.quality-info-addition {
  max-width: 400px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #3d3b3c;
}

.quality-info__partners {
  display: flex;
  flex-wrap: wrap;
  max-width: 755px;
  margin-top: 62px;
}

.quality-info__partners-img {
  margin-right: 90px;
  margin-bottom: 62px;
}

.quality-info__partners-img:nth-child(4) {
  width: 209px;
}

@media (max-width: 1800px) {
  .about-company-quality {
    top: -300px;
  }
}

@media (max-width: 1400px) {
  .about-company-quality {
    top: -130px;
  }

  .about-company-quality__info {
    display: block;
  }

  .quality-info__item:last-child {
    margin-top: 70px;
  }
}

@media (max-width: 992px) {
  .about-company-quality {
    top: 0;
    margin-top: 80px;
  }
}

@media (max-width: 576px) {
  .about-company-quality {
    margin-top: 50px;
    padding: 0 20px;
  }
}

@media (max-width: 440px) {
  .about-company-quality__title {
    font-size: 32px;
    line-height: 43px;
  }

  .quality-info__text {
    font-size: 16px;
    line-height: 115%;
  }
}
