.header {
  background: transparent;
  color: #fff;
  font-style: normal;
}

#header {
  position: fixed;
  width: 100%;
  z-index: 103;

  .container {
    padding: 40px 60px;
    display: flex;
    align-items: center;
  }
}

#header.bg {
  background: rgba(255, 255, 255, 0.7);
}

.logo {
  position: absolute;
}

.logo-img {
  animation: fade 5s linear infinite;
  animation-delay: 2.5s;
  transition: 0.5s;
}

.header-menu {
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 18px 9px;
  font-weight: bold;
  font-size: 0.6875em;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50px;
  border: 1px solid #fff;
  color: #0f0d0e;
  transition: 0.5s;

  span {
    font-size: 0.875em;
    margin-right: 6px;
  }
}

.header-btns {
  position: absolute;
  right: 60px;
  display: flex;
}

.header-btns__item {
  overflow: hidden;
  display: inline-block;
}

.header-links {
  font-weight: bold;
  font-size: 0.6875em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  transition: 0.35s;
  position: relative;
  display: inline-block;
}

.header-links-black {
  font-weight: bold;
  font-size: 0.6875em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  text-decoration: none;
  color: #151314;
  transition: 0.35s;
  position: relative;
  display: inline-block;
}

.header-links-black:last-child {
  margin-left: 106px;
}

.header-links:last-child {
  margin-left: 106px;
}

.header-links::before,
.header-links::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.header-links::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
}

.header-links:hover {
  transform: translateY(-100%);
}

.header-links-black::before,
.header-links-black::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.header-links-black::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
}

.header-links-black:hover {
  transform: translateY(-100%);
}

#menu {
  background-color: #fff;
  color: #0f0d0e;
  height: 100vh;
  position: fixed;
  z-index: 102;
  width: 100%;
  transform: translateY(-450%);
  transition: 0.5s;

  .container {
    position: relative;
    height: 100%;
  }

  .menu-container {
    margin: 0 auto;
    padding: 0 60px;
    padding-top: 160px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    position: relative;
    height: 100%;
  }

  .nav {
    display: flex;
    flex-direction: column;
    flex-basis: 50%;
    margin-top: 120px;
    max-width: 420px;
  }
}

#menu.visible {
  transform: translateY(0);
}

.header-menu.visible {
  color: #fff;
  background-color: #151314;
  border: 1px solid #151314;
}

.btn-black {
  color: #fff;
  background-color: #151314;
  border: 1px solid #151314;
}

.header-links.visible {
  color: #151314;
}

.nav-link {
  font-weight: normal;
  font-size: 1.25em;
  line-height: 120%;
  text-transform: uppercase;
  text-decoration: none;
  color: #0f0d0e;
  margin-bottom: 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}

.nav-link__list {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  opacity: 0;
  height: 0;
  transition: 0.35s;
}

.nav-link__list.active {
  opacity: 1;
  height: auto;
  transition: 0.35s;
}

.nav-link__list-item_link {
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  text-decoration: none;
  color: #0f0d0e;
}

.nav-link__list-item_link:hover {
  color: #c81947;
}

.nav-link__list-item {
  margin-bottom: 5px;
}

.nav-link__list-item:first-child::before {
  content: '';
  height: 1px;
  width: inherit;
  background: #e8e7e8;
  display: block;
  margin: 20px 0;
}

.nav-link__list-item::after {
  content: '';
  height: 1px;
  width: inherit;
  background: #e8e7e8;
  display: block;
  margin: 20px 0;
}

