.nav-link:hover::before {
  content: url("../images/menu-hover1.png");
  background-size: 50% auto;
  display: flex;
  position: absolute;
  left: -222px;
}

.nav-link:nth-child(2):hover::before {
  content: url("../images/menu-hover2.png");
}

.nav-link:nth-child(3):hover::before {
  content: url("../images/menu-hover3.png");
}

.nav-link:nth-child(4):hover::before {
  content: url("../images/menu-hover4.png");
}

.nav-link:hover {
  color: #c81947;
}

.about-company {
  display: flex;
  flex-direction: column;
}

.about-company__menu {
  margin-top: 120px;
  width: 220px;
  position: inherit;
}

.about-company__list-item {
  overflow: hidden;
  display: inline-block;
  margin-bottom: 20px;
}

.about-company__link {
  font-weight: bold;
  font-size: 0.6875em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #0f0d0e;
  text-decoration: none;
  position: relative;
  display: inline-block;
  transition: 0.35s;
  // padding: 15px 0;
}

.about-company__link::before,
.about-company__link::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.about-company__link::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
  // padding: 15px 0;
}

.about-company__link:hover {
  transform: translateY(-100%);
}

