.header-line {
  width: 80px;
}

.about-company__list-item:nth-child(4)::after {
  margin-top: 10px;
}

.social-link {
  position: absolute;
  right: 60px;
  bottom: 9%;
  overflow: hidden;
  display: inline-block;
}

.menu-lang__btns {
  margin-top: 100px;
}

.lang-btn {
  font-weight: bold;
  font-size: 0.6875em;
  line-height: 13px;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #0f0d0e;
  border: none;
  background-color: #fff;
  width: 64px;
  height: 64px;
  border-radius: 50%;
}

.lang-btn:hover {
  color: #c81947;
  border-color: #c81947;
}

.lang-btn:last-child {
  margin-left: 7px;
}

.lang-btn.active {
  border: 1px solid #0f0d0e;
}

.inst {
  font-weight: bold;
  font-size: 0.6875em;
  line-height: 13px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  text-decoration: none;
  color: #0f0d0e;
  position: relative;
  display: inline-block;
  transition: 0.35s;
}

.inst::before,
.inst::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.inst::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
}

.inst:hover {
  transform: translateY(-100%);
}

.menu-lang__btns-block {
  position: absolute;
  bottom: 10%;
}

@media (max-width: 1200px) {
  .social-link {
    bottom: 10.5%;
  }
}

@media (max-width: 992px) {
  .header-links:last-child {
    margin-left: 50px;
  }

  .header-links-black:last-child {
    margin-left: 50px;
  }

  .nav-link:hover::before {
    content: '';
  }
}

@media (max-width: 768px) {
  .header-links:last-child {
    margin-left: 15px;
  }

  .header-links-black:last-child {
    margin-left: 15px;
  }

  .header-menu {
    padding: 7px 13px 7px;
    font-size: 0.61em;
  }

  .nav-link {
    font-size: 15px;
    margin-bottom: 30px;
  }

  .hide-btn {
    display: none;
  }

  #menu .menu-container {
    display: flex;
    justify-content: space-between;
  }

  .social-link {
    bottom: 9.5%;
  }
}

@media (max-width: 576px) {
  #header .container {
    padding: 40px 20px;
  }

  #header .menu-container {
    padding: 25px 20px;
    padding-top: 120px;
  }

  #menu .menu-container {
    padding-left: 20px;
    padding-right: 20px;
  }

  #menu .nav {
    flex-basis: 100%;
    margin-top: 0;
  }

  .about-company__menu {
    margin-top: -140px;
    width: 100%;
  }

  .header-btns {
    right: 20px;
  }

  .nav-link {
    margin-bottom: 25px;
  }

  .about-company__link {
    padding: 5px 0;
  }

  .about-company__list-item {
    margin-bottom: 0;
  }

  .about-company__list-item:nth-child(4) {
    display: none;
  }

  .about-company__list-item:nth-child(5) {
    position: absolute;
    right: 0;
  }

  .about-company__list-item:nth-child(6) {
    position: absolute;
    right: 0;
    top: 28px;
  }

  .social-link {
    bottom: 7%;
    right: 20px;
  }
}

@media (max-height: 800px) {
  #menu .nav {
    margin-top: 0;
    max-width: 480px;
  }

  .about-company__menu {
    margin-top: 0;
  }

  .nav-link {
    font-size: 1em;
    margin-bottom: 20px;
  }

  .about-company__link {
    font-size: 0.6em;
    padding-bottom: 10px;
  }
}

@media (max-height: 600px) {
  .menu-lang {
    margin-top: 50px;
  }

  .nav-link {
    margin-bottom: 20px;
  }

  .menu-lang__btns {
    margin-left: 0;
    margin-right: auto;
  }

  .nav-link__list-item:first-child::before {
    margin: 5px 0;
  }

  .nav-link__list-item::after {
    margin: 5px 0;
  }
}

@media (max-height: 480px) {
  .nav-link {
    font-size: 0.56em;
    margin-bottom: 23px;
  }

  .about-company__link:nth-child(3)::after {
    margin-top: 15px;
    margin-bottom: 10px;
  }
}

@media (max-height: 420px) {
  .menu-lang {
    margin-top: 25px;
  }

  .social-link {
    bottom: 12%;
  }

  #menu .menu-container {
    padding-top: 125px;
  }
}

@media (max-height: 375px) {
  .menu-lang__btns-block {
    left: 60px;
  }

  #menu .menu-container {
    position: static;
  }
}

@media (max-width: 340px) {
  #menu .menu-container {
    padding-top: 120px;
  }

  .nav-link {
    margin-bottom: 11px;
    font-size: 14px;
  }

  .lang-btn {
    width: 30px;
    height: 30px;
  }

  .menu-lang__btns {
    margin-top: 80px;
  }

  .social-link {
    bottom: 5%;
  }
}
