.foam-header-block {
  color: #fff;
  font-style: normal;
  position: relative;
}

.foam-header-bg-container {
  width: 100%;
}

.foam-header-bg-container img {
  width: 100%;
  height: 500px;
}

.foam-title-block {
  width: 100%;
  position: absolute;
  bottom: 60px;
}

.foam-page-title {
  font-weight: normal;
  font-size: 96px;
  line-height: 115px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin-top: 344px;
}

.foam-page-title-left {
  text-align: left;
  padding: 0 60px;
}

@media (max-width: 620px) {
  .foam-page-title {
    font-size: 80px;
  }
}

@media (max-width: 565px) {
  .foam-page-title {
    font-size: 50px;
    line-height: 140px;
  }

  .foam-header-bg-container img {
    object-fit: cover;
  }
}

@media (max-width: 420px) {
  .foam-title-block {
    bottom: 105px;
  }

  .foam-page-title {
    font-size: 50px;
    line-height: 57px;
  }
}

@media (max-width: 400px) {
  .foam-page-title-left {
    font-size: 36px;
  }
}
