.foam-block {
  padding: 0 60px;
  background-color: #fff;
  height: 100vh;
}

.about-company .container-animate {
  height: 100%;
  top: 0;
  position: absolute;
  width: 100%;
}

.about-company .container-animate-wrapper {
  height: 100%;
  top: 0;
  position: absolute;
  width: 100%;
}

.panel .container-animate {
  opacity: 0;
}

.foam-title {
  font-weight: bold;
  font-size: 1.25em;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  padding-top: 164px;
}

.foam {
  display: flex;
  justify-content: space-between;
}

.foam-item {
  display: flex;
}

.foam-item__wrap * {
  transition: all 0.35s ease;
}

.foam-item__wrap {
  margin-top: 60px;
}

.foam-item__wrap:nth-child(2) {
  margin-left: 32px;
  margin-right: 32px;
}

.foam-item__text {
  margin-bottom: 15px;
}

.foam-item__text-link {
  font-weight: normal;
  font-size: 0.75em;
  text-transform: uppercase;
  color: #9f9e9f;
  text-decoration: none;
  display: inline-block;
}

.foam-item__text-link::before,
.foam-item__text-link::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.foam-item__text-link::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
}

.foam-item__animate-link:hover .foam-item__text-link {
  transform: translateY(-100%);
}

.foam-item__title {
  font-weight: bold;
  font-size: 0.75em;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 20px;
  display: inline-block;
  overflow: hidden;
}

.foam-item__animate-link {
  overflow: hidden;
}

.foam-line {
  content: '';
  display: block;
  max-width: 220px;
  height: 1px;
  background: #ededed;
  margin-bottom: 20px;
  border: none;
}

.foam-link {
  cursor: pointer;
  text-decoration: none;
  color: #0f0d0e;
  position: relative;
  display: inline-block;
}

.foam-link::before,
.foam-link::after {
  position: absolute;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.foam-link::after {
  position: absolute;
  left: 0;
  top: 100%;
  content: attr(data-hover);
}

.foam-item__title:hover .foam-link {
  transform: translateY(-100%);
}

.foam-item:last-child {
  width: 505px;
  flex-wrap: wrap;

  .foam-item__title {
    width: 220px;
  }

  .foam-item__title::after {
    display: none;
  }
}

.foam-shipment__wrap {
  display: flex;
  align-items: center;
}

.foam-shipment::before {
  content: '';
  display: block;
  max-width: 736px;
  height: 1px;
  background-color: #ededed;
  margin-bottom: 40px;
}

.shipment-title {
  font-weight: 900;
  font-size: 0.75em;
  line-height: 14px;
  text-transform: uppercase;
  color: #0f0d0e;
}

.shipment {
  display: flex;
  align-items: center;
  margin-left: 57px;
}

.shipment-item {
  font-weight: normal;
  font-size: 0.75em;
  line-height: 14px;
  text-transform: uppercase;
  color: #777677;
  display: flex;
  align-items: center;
  margin-right: 40px;

  img {
    margin-right: 10px;
  }
}

.foam-shipment-mobile {
  display: none;
}

@media (max-width: 1390px) {
  .foam-item__wrap {
    margin-right: 30px;
  }

  .foam-item__wrap:nth-child(2) {
    margin-left: 0;
    margin-right: 0;
  }

  .foam-item {
    justify-content: space-between;
  }
}

@media (max-width: 1200px) {
  .foam {
    display: block;
  }

  .foam-block {
    height: initial;
  }

  .foam-item:last-child {
    width: 100%;
    margin-bottom: 80px;
  }
}

@media (max-width: 1024px) {
  .panel .container-animate {
    opacity: 1;
  }

  .about-company__img-block {
    display: none;
  }

  .foam-item__text-link::after {
    display: none;
  }
}

@media (max-width: 992px) {
  .foam-title {
    padding-top: 40px;
  }

  .foam-block {
    margin-top: 60px;
  }

  .foam-item__wrap {
    margin-right: 15px;
    width: 199px;
  }

  .foam-item__wrap:nth-child(2) {
    margin-left: 0;
    margin-right: 15px;
  }

  .foam-item:last-child .foam-item__title {
    width: 199px;
  }

  .shipment {
    margin-left: 20px;
  }

  .shipment-item {
    margin-right: 20px;
  }
}

@media (max-width: 768px) {
  .foam-item {
    display: flex;
    flex-wrap: wrap;
  }

  .foam-shipment__wrap {
    flex-wrap: wrap;
  }

  .shipment-title {
    flex-basis: 100%;
  }

  .shipment {
    margin-left: 0;
    margin-top: 20px;
  }

  .shipment-item {
    margin-right: 10px;
  }
}

@media (max-width: 576px) {
  .foam-block {
    margin-top: 5px;
    padding: 0 20px;
  }

  .foam-item__wrap {
    margin-top: 30px;
    width: 125px;
  }

  .foam-item:last-child {
    margin-bottom: 40px;
  }

  .foam-shipment {
    display: none;
  }

  .shipment {
    flex-wrap: wrap;
  }

  .foam-shipment-mobile {
    display: flex;
    margin-top: 20px;
  }

  .shipment-item {
    margin-bottom: 15px;
    width: 80px;
  }

  .foam-item__title {
    max-width: 125px;
  }
}

@media (max-width: 450px) {
  .foam-block {
    padding: 0 20px;
  }

  .foam-item__wrap {
    width: 125px;
  }

  .shipment-item img {
    margin-right: 5px;
  }

  .shipment-item {
    margin-right: 5px;
  }
}
