.cutting-adv {
  padding: 0 60px;
  margin-top: 160px;
  padding-bottom: 150px;
}

.cutting-adv-wrapper {
  display: flex;
  justify-content: space-between;
}

.cutting-adv__img {
  margin-right: 20px;
}

.cutting-adv__text {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 724px;
  margin-top: 200px;
}

.goods-item_description_mockap {
  color: #c81947;
}

@media (max-width: 992px) {
  .cutting-adv__text {
    font-size: 16px;
    margin-top: 100px;
  }

  .cutting-adv {
    padding-bottom: 100px;
    margin-top: 100px;
  }
}

@media (max-width: 576px) {
  .cutting-adv {
    margin-top: 40px;
    padding: 0 20px;
  }

  .cutting-adv__img {
    margin-right: 10px;
  }

  .cutting-adv-wrapper {
    display: block;
  }

  .cutting-adv-wrapper img {
    margin-bottom: 40px;
  }
}
