.blog-content {
  padding: 0 60px;
  position: relative;
  z-index: 100;
  margin-top: -470px;
}

.blog-content__wrapper {
  display: flex;
  margin-top: 0;
  margin-bottom: 120px;
}

.blog-content__wrapper-item {
  max-width: 220px;
}

.blog-content__wrapper-item_big {
  margin-right: auto;
  max-width: 472px;
}

.blog-content__wrapper-item img {
  max-width: 220px;
  max-height: 160px;
}

.blog-content__wrapper-item_big img {
  max-width: 472px;
  max-height: 436px;
}

.blog-content__wrapper-item_position {
  margin-right: 32px;
}

.blog-content__wrapper-item_center {
  margin-left: auto;
  margin-right: auto;
}

@media (max-width: 1600px) {
  .blog-content {
    margin-top: -255px;
  }
}

@media (max-width: 1200px) {
  .blog-content {
    margin-top: -105px;
  }
}

@media (max-width: 1110px) {
  .blog-content {
    margin-top: 50px;
    width: 100%;
  }

  .blog-content__wrapper-item_big {
    margin-right: 0;
    width: 100% !important;
    max-width: 100%;
  }

  .blog-content__wrapper-item_big img {
    width: 100%;
    max-width: 100%;
    object-fit: cover;
  }

  .blog-content__wrapper {
    flex-wrap: wrap;
    margin-bottom: 80px;
  }

  .blog-content__wrapper-item {
    margin-bottom: 30px;
  }

  .blog-content__wrapper-item_center {
    margin-left: 0;
    margin-right: 0;
  }

  .mobile-position {
    order: 1;
  }
}

@media (max-width: 576px) {
  .blog-content__wrapper-item_big img {
    max-height: 250px;
  }
}

@media (max-width: 430px) {
  .blog-content {
    padding: 0 20px;
  }
}
