.blog-bg {
  color: #fff;
  font-style: normal;
  position: relative;
  top: -200px;
  overflow: hidden;
}

.video-container {
  width: 100%;
}

.video-container video {
  max-width: 100%;
  height: 100%;
}

.video-conterner-white-shadow {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)), url(ezgif);
  z-index: 100;
}

.blog-bg-title {
  position: absolute;
  left: 0;
  top: 45%;
  padding: 60px 40px;
  width: 100%;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 115px;
  text-transform: uppercase;
  color: #151314;
  z-index: 100;
}

@media (max-width: 1600px) {
  .blog-bg {
    top: 0;
  }

  .blog-bg-title {
    top: 30%;
  }
}

@media (max-width: 576px) {
  .blog-bg-title {
    top: 15%;
  }
}

@media (max-width: 430px) {
  .blog-bg-title {
    font-size: 55px;
  }
}
