.about-company-priority {
  margin-top: 160px;
  padding: 0 60px;
  display: flex;
  justify-content: space-between;
  position: relative;
  top: -500px;
}

.about-company-priority__title {
  font-style: normal;
  font-weight: normal;
  font-size: 70px;
  line-height: 84px;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 665px;
}

.about-company-priority__text {
  max-width: 663px;
}

.about-company-priority__text-item {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #3d3b3c;
  margin-bottom: 30px;
}

.about-company-priority__text-item:last-child {
  font-weight: bold;
}

@media (max-width: 1800px) {
  .about-company-priority {
    top: -300px;
  }
}

@media (max-width: 1400px) {
  .about-company-priority {
    top: -130px;
  }

  .about-company-priority__title {
    margin-right: 30px;
    font-size: 57px;
    line-height: 64px;
  }
}

@media (max-width: 992px) {
  .about-company-priority {
    top: 0;
    margin-top: 80px;
  }

  .about-company-priority__text {
    margin-top: 50px;
  }
}

@media (max-width: 768px) {
  .about-company-priority {
    display: block;
  }

  .about-company-priority__text {
    margin-top: 100px;
  }

  .about-company-priority__text-item {
    max-width: 600px;
  }
}

@media (max-width: 576px) {
  .about-company-priority {
    margin-top: 50px;
    padding: 0 20px;
  }
}

@media (max-width: 440px) {
  .about-company-priority__title {
    font-size: 35px;
    line-height: 38px;
  }

  .about-company-priority__text {
    margin-top: 45px;
  }

  .about-company-priority__text-item {
    font-size: 16px;
    line-height: 110%;
  }
}
