.error-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-wrap: wrap;
  flex-direction: column;
}

.error-page__title {
  font-style: normal;
  font-weight: bold;
  font-size: 198px;
  line-height: 238px;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
}

.error-page-subtitle {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-top: 14px;
}
