.foam-service-title {
  margin-top: 325px;
}

.service-title {
  max-width: 1219px;
  margin: 0 auto;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 115px;
  text-align: center;
  text-transform: uppercase;
  color: #151314;
  margin-bottom: 60px;
}

.service-title__bg img {
  width: 100%;
}

.service-description {
  padding: 0 60px;
  margin-top: 160px;
  display: flex;
  justify-content: space-between;
}

.service-description__item {
  max-width: 724px;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
}

@media (max-width: 1470px) {
  .service-description__item {
    margin-right: 40px;
  }

  .cutting-adv__img img {
    max-width: 80%;
  }
}

@media (max-width: 992px) {
  .service-title {
    font-size: 67px;
  }

  .service-description__item {
    font-size: 16px;
  }

  .service-description {
    margin-top: 80px;
  }
}

@media (max-width: 768px) {
  .foam-service-title {
    margin-top: 186px;
  }

  .service-title {
    font-size: 55px;
    line-height: 85px;
  }
}

@media (max-width: 576px) {
  .service-title {
    font-size: 40px;
  }

  .service-description {
    display: block;
    padding: 0 20px;
    margin-top: 50px;
  }

  .service-description__item {
    margin-bottom: 40px;
  }

  .service-title__bg img {
    height: 260px;
    object-fit: cover;
  }

  .duplicate-note {
    margin-top: 88px;
    padding-bottom: 85px;
  }
}

@media (max-width: 420px) {
  .service-title {
    line-height: 57px;
    font-size: 33px;
  }

  .service-description__item {
    font-size: 14px;
  }
}
