.blog {
  padding: 0 60px;
  color: #0f0d0e;
  height: 100vh;
  background-color: #fff;
}

.blog-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 240px;
}

.blog-title {
  font-weight: normal;
  font-size: 2.875em;
  line-height: 55px;
  text-transform: uppercase;
}

.news-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #0f0d0e;

  img {
    margin-left: 12px;
  }
}

.blog-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 238px;
}

.blog-wrapper__item {
  width: 220px;
  position: relative;
}

.blog-wrapper__item:nth-child(4) {
  width: 472px;
  margin-top: -276px;
}

