.blog-wrapper__date {
  font-weight: normal;
  font-size: 0.625em;
  line-height: 160%;
  text-transform: uppercase;
  color: #9f9e9f;
  margin-top: 20px;
}

.blog-wrapper__title {
  font-weight: normal;
  font-size: 12px;
  line-height: 160%;
  text-transform: uppercase;
  margin-top: 0.625em;
}

.blog-btn__wrapper {
  justify-content: center;
  margin-top: 20px;
  display: none;
}

.blog-btn {
  padding: 12px 50px 11px;
  background: #c81947;
  border-radius: 50px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  border: none;
  text-decoration: none;
}

.blog-wrapper__item-link {
  height: 100%;
  width: 100%;
  position: absolute;
}

@media (max-width: 1500px) {
  .blog-wrapper {
    margin-top: 100px;
  }

  .blog-wrapper__item {
    width: 140px;
  }

  .blog-wrapper__item:nth-child(4) {
    width: 255px;
    margin-top: -133px;
  }
}

@media (max-width: 1400px) {
  .blog {
    height: inherit;
    padding-bottom: 100px;
  }
}

@media (max-width: 992px) {
  .blog {
    height: initial;
    padding-bottom: 70px;
    padding-top: 50px;
  }

  .blog-wrapper {
    margin-top: 110px;
  }

  .blog-header {
    padding-top: 0;
  }

  .blog-wrapper__item {
    width: 100px;
  }

  .blog-wrapper__title {
    font-weight: normal;
    font-size: 10px;
    line-height: 118%;
    text-transform: uppercase;
    margin-top: 0.625em;
  }

  .blog-wrapper__item:nth-child(4) {
    width: 200px;
    margin-top: -111px;
  }
}

@media (max-width: 768px) {
  .blog-wrapper {
    flex-direction: column;
  }

  .blog-wrapper__item {
    margin-bottom: 20px;
    width: 100%;
  }

  .blog-wrapper__item:nth-child(4) {
    margin-top: 0;
    width: 100%;
    order: -1;
  }

  .mobile-hide {
    display: none;
  }

  .blog-btn__wrapper {
    display: flex;
  }
}

@media (max-width: 550px) {
  .blog {
    margin: 30px 0;
    padding: 0 30px;
  }

  .blog-wrapper {
    margin-top: 20px;
  }
}

@media (max-width: 400px) {
  .blog-title {
    font-size: 2em;
  }

  .news-link {
    font-size: 14px;
  }
}

@media (max-height: 800px) {
  .blog-header {
    padding-top: 30px;
  }
}
