.about-collection {
  padding: 120px 60px;

  .collection-item {
    max-width: 415px;
  }

  .collection-item__text {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 180%;
    text-transform: uppercase;
    color: #0f0d0e;
    margin-top: 30px;
  }
}

@media (max-width: 992px) {
  .collection-item {
    margin-bottom: 60px;
  }

  .about-collection .collection-item {
    max-width: 200px;
  }
}

@media (max-width: 768px) {
  .foam-description-collection {
    flex-direction: column;
  }

  .about-collection .collection-item {
    max-width: 100%;
  }
}

@media (max-width: 576px) {
  .about-collection {
    padding: 20px;
  }
}
