.duplicate-description {
  padding: 0 60px;
}

.duplicate-description-wrapper {
  display: flex;
  justify-content: space-between;
}

.duplicate-description-wrapper__item:first-child {
  max-width: 724px;
}

.duplicate-description__text {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-bottom: 100px;
}

.duplicate-description__list-item {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin-bottom: 30px;
}

.duplicate-description__wrapper-btn {
  margin-top: 112px;
}

.duplicate-description__btn {
  padding: 12px 22px 11px;
  border: 1px solid #0f0d0e;
  border-radius: 50px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
  background-color: #fff;
}

.duplicate-description__btn:hover {
  background: #c81947;
  color: #fff;
  border: none;
}

@media (max-width: 992px) {
  .duplicate-description-wrapper {
    display: block;
  }
}

@media (max-width: 576px) {
  .duplicate-description {
    padding: 0 20px;
  }

  .duplicate-description__text {
    font-size: 26px;
    line-height: 41px;
    margin-bottom: 50px;
  }

  .duplicate-description__list-item {
    font-size: 16px;
    line-height: 100%;
  }

  .duplicate-description__wrapper-btn {
    margin-top: 65px;
  }

  .duplicate-description__wrapper-btn:last-child {
    text-align: center;
  }
}

@media (max-width: 430px) {
  .duplicate-description__text {
    font-size: 20px;
    line-height: 32px;
  }

  .duplicate-description__list-item {
    font-size: 14px;
  }

  .duplicate-description__btn {
    padding: 10px;
    font-size: 9px;
  }

  .duplicate-tab__item {
    font-size: 14px !important;
    line-height: 100%;
  }

  .duplicate-tab__btn {
    padding: 7px;
    font-size: 10px;
  }
}
