.about-company-title {
  color: #fff;
  font-style: normal;
  position: relative;
  top: -500px;
}

.video-container {
  width: 100%;
}

.video-container video {
  max-width: 100%;
  height: 100%;
}

.about-company-title__title {
  position: absolute;
  left: 0;
  bottom: 124px;
  padding: 0 60px;
  width: 100%;
  z-index: 100;
  font-style: normal;
  font-weight: normal;
  font-size: 96px;
  line-height: 115px;
  text-transform: uppercase;
  color: #fff;
}

.video-conterner-shadow {
  position: absolute;
  left: 0;
  bottom: 3px;
  width: 100%;
  height: 1650px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(.png);
  z-index: 100;
}

@media (max-width: 1600px) {
  .about-company-description {
    top: 0;
  }
}

@media (max-width: 1800px) {
  .about-company-title {
    top: -300px;
  }
}

@media (max-width: 1400px) {
  .about-company-title {
    top: -130px;
  }
}

@media (max-width: 992px) {
  .about-company-title {
    top: 0;
  }
}

@media (max-width: 768px) {
  .about-company-title__title {
    bottom: 40px;
    font-size: 65px;
  }
}

@media (max-width: 576px) {
  .about-company-title__title {
    bottom: 40px;
    font-size: 40px;
  }
}

@media (max-width: 390px) {
  .about-company-title__title {
    bottom: 0;
    font-size: 27px;
    padding: 0 40px;
  }
}
