.title-gif {
  color: #fff;
  font-style: normal;
  position: relative;
}

.video-container {
  width: 100%;
}

.video-container video {
  max-width: 100%;
  height: 100%;
}

#video-bg {
  height: 100vh;
  width: 100%;
  object-fit: cover;
}

.titles {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 60px 40px;
  width: 100%;
}

.pre-title {
  color: #fff;
  font-weight: normal;
  font-size: 4.875em;
  line-height: 94px;
  text-transform: uppercase;
}

.main-title::before {
  content: '';
  display: block;
  height: 4px;
  width: inherit;
  background-color: #fff;
  margin-bottom: 40px;
  margin-top: 30px;
}

.main-title img {
  width: 100%;
}

@media (max-width: 1200px) {
  .pre-title {
    font-size: 4.1em;
  }
}

@media (max-width: 992px) {
  .pre-title {
    font-size: 2.1em;
    line-height: 20px;
  }

  .main-title::before {
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .titles {
    padding: 30px 40px;
  }

  .pre-title {
    font-size: 1.876em;
  }

  .main-title::before {
    margin-top: 15px;
  }

  .title-gif {
    top: -20px;
  }
}

@media (max-width: 450px) {
  .titles {
    padding: 30px 20px;
  }
}

@media (max-width: 390px) {
  .pre-title {
    font-size: 1.2em;
  }

  .main-title::before {
    margin-top: 5px;
    margin-bottom: 10px;
  }
}
