.about-company {
  position: relative;
}

.video-container video {
  max-width: 100%;
  height: 100%;
}

#about-video-id {
  height: 100vh;
  object-fit: cover;
  width: 100%;
}

.about-company__wrap {
  position: absolute;
  top: 35%;
  padding: 0 60px;
  width: 100%;

  .main-title::before {
    display: none;
  }

  .main-title::after {
    content: '';
    display: block;
    height: 4px;
    width: inherit;
    background-color: #fff;
    margin-bottom: 40px;
    margin-top: 80px;
  }
}

.work-expr {
  display: flex;
  justify-content: space-between;
  color: #fff;
  margin-top: 80px;
}

.work-expr__title {
  font-weight: normal;
  font-size: 4.375em;
  line-height: 84px;
  text-transform: uppercase;
}

.work-expr__subtitle {
  width: 184px;
  font-weight: normal;
  font-size: 0.75em;
  line-height: 180%;
  text-transform: uppercase;
  margin-top: 20px;
}

.work-expr__item {
  position: relative;
  z-index: 101;
}

.work-expr__item:last-child {
  margin-top: 30px;
  cursor: pointer;

  .work-expr__title {
    transition: 0.3s ease-out;
  }

  .work-expr__subtitle {
    margin-top: -10px;
    transition: 0.3s ease-out;
  }

  .work-expr__img {
    width: 23px;
    transition: 0.3s ease-out;
  }
}

.work-expr__item:last-child:hover {
  .work-expr__subtitle {
    font-size: 1.25em;
    line-height: 120%;
    margin-top: -10px;
  }

  .work-expr__title {
    margin-left: 47px;
    width: 60px;
  }

  .work-expr__img {
    width: 35px;
  }
}

.about-company__img-block {
  padding: 0 60px;
  transform: translateY(400px);
  position: relative;
  height: 100%;
  z-index: 90;

  .about-company__img_item {
    position: absolute;
    top: 50%;
    z-index: 100;
  }

  .about-company__img_item:nth-child(2) {
    left: 35%;
    top: 13%;
  }

  .about-company__img_item:last-child {
    left: 67%;
    top: 41%;
  }
}

.work-expr-link {
  text-decoration: none;
  color: #fff;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 99999;
}

@media (max-width: 1350px) {
  .about-company {
    margin-top: 150px;
  }
}

@media (max-width: 1200px) {
  .about-company__img_item {
    width: 15%;
  }

  #about-video-id {
    height: 100%;
  }

  .work-expr {
    margin-top: 0;
  }

  .work-expr__title {
    font-size: 46px;
    line-height: 46px;
  }

  .work-expr__item:last-child {
    margin-top: 0;
  }

  .work-expr__item:last-child .work-expr__subtitle {
    margin-top: 18px;
  }

  .work-expr__item:last-child:hover {
    .work-expr__subtitle {
      font-size: 1.25em;
      line-height: 120%;
      margin-top: 20px;
    }

    .work-expr__title {
      margin-left: 47px;
      width: 60px;
    }

    .work-expr__img {
      width: 25px;
    }
  }
}

@media (max-width: 992px) {
  .about-company__wrap .main-title::after {
    margin-bottom: 18px;
    margin-top: 30px;
  }

  .about-company__img-block {
    display: none;
  }

  .work-expr__title {
    font-size: 36px;
    line-height: 36px;
  }

  .work-expr__subtitle {
    font-size: 0.7em;
    margin-top: 10px;
  }

  .work-expr__item:last-child .work-expr__subtitle {
    margin-top: 10px;
  }

  .work-expr__item:last-child .work-expr__img {
    width: 20px;
  }

  .work-expr__item:last-child:hover {
    .work-expr__subtitle {
      font-size: 1em;
      line-height: 120%;
      margin-top: 10px;
    }

    .work-expr__title {
      margin-left: 37px;
      width: 60px;
    }

    .work-expr__img {
      width: 20px;
    }
  }

  .work-expr-link {
    width: 20%;
    height: 40%;
    top: 0;
    z-index: 100;
  }
}

@media (max-width: 768px) {
  .work-expr__subtitle {
    width: 125px;
    line-height: 120%;
    font-size: 0.56em;
  }

  .work-expr__title {
    font-size: 30px;
  }

  .work-expr__item:last-child .work-expr__img {
    width: 17px;
  }

  .work-expr__item:last-child:hover {
    .work-expr__subtitle {
      font-size: 0.56em;
      line-height: 120%;
      margin-top: 10px;
    }

    .work-expr__title {
      margin-left: 0;
      width: 17px;
    }

    .work-expr__img {
      width: 17px;
    }
  }
}

@media (max-width: 550px) {
  .about-company__wrap {
    padding: 0 20px;
    top: 22%;
  }

  .mobile-main-title {
    padding-bottom: 20px;
  }

  .about-company {
    margin-top: 60px;
  }

  .work-expr {
    flex-wrap: wrap;
  }

  #about-video-id {
    height: 70vh;
    object-fit: cover;
    width: 100%;
  }

  .work-expr__item {
    margin-bottom: 30px;
    flex-basis: 35%;
  }

  .work-expr__subtitle {
    font-size: 14px;
  }

  .work-expr-link {
    width: 20%;
    height: 40%;
    top: -5px;
    z-index: 100;
  }
}
