.table-popup {
  position: fixed;
  right: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition: 1s;
  z-index: 107;
  height: 100vh;
  display: none;
}

.table-popup-content {
  position: fixed;
  width: 99%;
  height: 98vh;
  height: 100%;
  background-color: #f4f4f4;
  display: flex;
  flex-direction: column;
  z-index: 103;
  transition: 1s;
  margin: 10px;
  padding: 43px 30px 50px 100px;
}

.table-popup__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
}

.close-table-popup {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 80px;
  background-color: #fff;
  font-size: 20px;
  border-radius: 50%;
  margin-left: -40px;
  position: absolute;
  top: 27px;
  right: 30px;
  cursor: pointer;
}

.table-title {
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  text-align: left;
  padding-bottom: 30px;
}

.table-wrapper {
  overflow: auto;
  width: 98%;
  height: 100%;
  max-width: 1920px;
  margin-top: 110px;
}

.table-wrapper table {
  width: 100%;
  min-width: 250%;
  overflow: auto;
  border-collapse: collapse;
  margin-bottom: 100px;
}

.table-wrapper table td {
  border-top: 1px solid #9f9e9f;
  border-bottom: 1px solid #9f9e9f;
  padding: 20px 0;
  width: 290px;
  padding-right: 60px;
}

.table-wrapper table th {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-transform: uppercase;
  color: #0f0d0e;
  text-align: left;
  border-top: 1px solid #0f0d0e;
  padding: 30px 0;
  vertical-align: top;
  max-width: 290px;
  padding-right: 35px;
}

