@media (max-width: 1400px) {
  .table-popup-content {
    position: fixed;
    width: 98%;
    height: 98vh;
    height: 98%;
  }

  .table-popup__title {
    margin-right: 100px;
  }

  .table-wrapper {
    margin-top: 60px;
  }

  .table-wrapper table th {
    padding: 15px 0;
    padding-right: 88px;
    line-height: 130%;
  }
}

@media (max-width: 940px) {
  .table-popup-content {
    position: fixed;
    width: 97%;
    height: 98vh;
    height: 98%;
  }

  .table-popup__title {
    font-size: 30px;
    line-height: 40px;
  }

  .table-wrapper {
    margin-top: 20px;
  }

  .table-title {
    max-width: 420px;
    font-size: 16px;
    padding-bottom: 15px;
  }

  .table-wrapper table {
    min-width: 720%;
  }

  .table-wrapper table th {
    padding: 15px 0;
    padding-right: 50px;
    line-height: 100%;
  }

  .table-wrapper table td {
    border-top: 1px solid #9f9e9f;
    border-bottom: 1px solid #9f9e9f;
    padding: 10px 0;
    max-width: 155px;
    padding-right: 25px;
  }
}

@media (max-width: 546px) {
  .table-popup-content {
    position: fixed;
    width: 95%;
    height: 98vh;
    height: 98%;
    padding: 20px;
  }

  .table-popup__title {
    font-size: 25px;
    line-height: 33px;
  }

  .table-title {
    max-width: 315px;
    font-size: 14px;
  }
}

.table-wrapper h2 {
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  text-align: left;
  padding-bottom: 30px;
}
