.duplicate-serv {
  padding: 0 60px;
  margin-top: 100px;
}

.duplicate-serv__title {
  font-style: normal;
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
}

.duplicate-serv__title span {
  font-size: 30px;
  text-transform: lowercase;
  display: block;
}

.duplicate-wrapper {
  margin-top: 67px;
  display: flex;
}

.duplicate-slider {
  position: relative;
  margin-right: 32px !important;
  width: 50.83vw;
  margin-left: 0 !important;
}

.duplicate-slide {
  width: 50.83vw;
  position: absolute;
}

.duplicate-slide img {
  height: 802px;
  position: absolute;
}

.active-slide {
  opacity: 1;
  transition: opacity 0.5s;
}

.duplicate-arrow {
  position: absolute;
  top: 50%;
  z-index: 102;
}

.duplicate-arrow.prev {
  left: 20px;
}

.duplicate-arrow.next {
  right: 20px;
}

.duplicate-tab__item {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #151314;
  width: 472px;
}

.duplicate-tab__item:not(:first-child) {
  padding-top: 34px;
}

.duplicate-tab__item::before {
  content: '';
  display: block;
  max-width: 472px;
  height: 1px;
  background-color: #0f0d0e;
  margin-bottom: 34px;
}

.duplicate-tab__btn {
  padding: 12px 22px 11px;
  background: #c81947;
  border-radius: 50px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  border: none;
}

.duplicate-note {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 127px;
  padding-bottom: 150px;
}

.duplicate-note__title {
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 180%;
  text-align: center;
  text-transform: uppercase;
  color: #0f0d0e;
}

@media (max-width: 1500px) {
  .duplicate-wrapper {
    justify-content: space-between;
  }

  .duplicate-slide img {
    height: 495px;
    width: 50.83vw;
  }

  .duplicate-tab__item::before {
    margin-bottom: 18px;
  }

  .duplicate-tab__item {
    font-size: 16px;
  }

  .duplicate-tab__item:not(:first-child) {
    padding-top: 20px;
  }
}

@media (max-width: 1270px) {
  .duplicate-wrapper {
    flex-wrap: wrap;
  }

  .duplicate-slider {
    width: 100%;
    height: 880px;
    margin-right: 0 !important;
  }

  .duplicate-slide img {
    width: 100%;
    height: 880px;
  }

  .active-slide {
    width: 100%;
  }

  .duplicate-tabs {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .duplicate-tab__item {
    margin-top: 50px;
  }

  .duplicate-tab__item:not(:first-child) {
    padding-top: 0;
  }

  .duplicate-tab__item::before {
    display: none;
  }
}

@media (max-width: 992px) {
  .duplicate-wrapper {
    margin-top: 0;
  }

  .duplicate-slider {
    width: 100%;
    height: 600px;
  }

  .duplicate-slide img {
    width: 100%;
    height: 600px;
    object-fit: cover;
  }
}

@media (max-width: 576px) {
  .duplicate-serv {
    padding: 0 20px;
    margin-top: 75px;
  }

  .duplicate-tabs {
    flex-direction: column;
  }

  .duplicate-tab__item {
    margin-top: 35px;
    width: initial;
  }

  .duplicate-note {
    margin-top: 88px;
    padding-bottom: 85px;
  }

  .duplicate-tab__item:last-child {
    text-align: center;
  }

  .duplicate-slider {
    height: 470px;
  }

  .duplicate-slide img {
    object-fit: contain;
    height: 470px;
  }

  .duplicate-arrow.next {
    right: 10px;
  }

  .duplicate-arrow.prev {
    left: 10px;
  }
}

@media (max-width: 430px) {
  .duplicate-serv__title {
    font-size: 21px;
    line-height: 40px;
  }

  .duplicate-arrow img {
    width: 70%;
  }

  .duplicate-slider {
    height: 315px;
  }

  .duplicate-slide img {
    object-fit: inherit;
    height: 330px;
  }

  .duplicate-arrow.next {
    right: 0;
  }
}
