.blog-content-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 150px 60px 0;
}

.back-btn {
  display: flex;
  align-items: center;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  color: #0f0d0e;
  cursor: pointer;
}

.back-btn img {
  margin-right: 5px;
}

.blog-content__title {
  font-weight: normal;
  font-size: 46px;
  line-height: 55px;
  text-transform: uppercase;
  color: #0f0d0e;
  max-width: 700px;
  margin-left: -292px;
}

.blog-content__date {
  font-weight: normal;
  font-size: 12px;
  line-height: 180%;
  text-align: right;
  text-transform: uppercase;
  color: #0f0d0e;
}

.blog-news__img-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 80px 0;
}

.blog-news__img-wrapper img {
  width: 472px;
  height: 320px;
  position: relative;
}

@media (max-width: 1400px) {
  .blog-content__title {
    margin-left: 0;
  }
}

@media (max-width: 992px) {
  .blog-content-title {
    flex-wrap: wrap;
  }

  .blog-content__title {
    flex-basis: 100%;
    order: 3;
    margin-top: 20px;
  }
}

@media (max-width: 768px) {
  .blog-content-title {
    padding: 150px 20px 0;
  }

  .blog-content__title {
    font-size: 30px;
    line-height: 40px;
  }
}
