.blog-news {
  padding-top: 85px;
}

.blog-news__img {
  width: 100%;
  object-fit: contain;
}

.blog-news-container {
  margin: 0 auto;
  max-width: 992px;
}

.blog-news__wrapper-title {
  font-weight: normal;
  font-size: 20px;
  line-height: 120%;
  text-transform: uppercase;
  color: #0f0d0e;
  margin: 50px 0;
}

.blog-news__wrapper-text {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 180%;
  color: #0f0d0e;
  margin-bottom: 25px;
}

@media (max-width: 1450px) {
  .blog-news__img-wrapper img {
    width: 278px;
    height: 201px;
  }
}

@media (max-width: 1050px) {
  .blog-news-container {
    width: 95%;
  }

  .blog-news__img-wrapper img {
    width: 226px;
    height: 161px;
  }
}

@media (max-width: 700px) {
  .hide-block {
    display: none;
  }

  .blog-news__img-wrapper img {
    width: 373px;
    height: 264px;
  }
}

@media (max-width: 576px) {
  .news__wrapper-title {
    font-size: 16px;
  }

  .blog-news__wrapper-text {
    font-size: 14px;
  }

  .blog-news__img-wrapper img {
    width: 297px;
    height: 264px;
  }
}
